			async H5login(flag){
				let params = {
					partner_id: uni.getStorageSync("partner_id"),
					imei: uni.getStorageSync("imei")
				};
				
				let res = await H5login(params);
				if(res.code == 200){
					let token = res.data.token;
					console.log(token);
					if(token){
						uni.setStorageSync('token', token);
					}
					
					if(flag == 1){
						uni.navigateTo({
						  url: "../baojia/baojia"
						})
					}else{
						uni.navigateTo({
						  url: "../bill_detail/bill_detail"
						})
					}
					
				}
			},
			
